import React from "react"
import { Table, Button } from "reactstrap";


import AnalysesTableHeader from "./AnalysisTableHeader";

import { stdDateTimeFormatToCustom } from "../utils";

export default function AnalysesTable({ analyses, onViewAnalysis, onAnalyze }) {
  return (
    <>
      {/* <div style={{ overflowY: "auto", maxHeight: "500px" }}> */}
        <Table>
          <AnalysesTableHeader />
          <tbody className="scroll">
            {analyses.length === 0 ? (
              <tr>
                <td colSpan={5}>There aren't any existing analyses</td>
              </tr>
            ) : (
              analyses.map((analysis) => (
                <tr key={analysis.id}>
                  <td>{analysis.id}</td>
                  <td>{analysis.analyzer}</td>
                  <td>{stdDateTimeFormatToCustom(analysis.timestamp)}</td>
                  <td>
                    <Button
                      size="sm"
                      color="primary"
                      onClick={() => onViewAnalysis(analysis.id)}
                    >
                      View
                    </Button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </Table>
      {/* </div> */}
      <Button size="sm" color="primary" onClick={() => onAnalyze()}>
        Create New Analysis
      </Button>
    </>
  );
}
