import React from "react";
import { Table, Button } from "reactstrap";

import { stdDateTimeFormatToCustom } from "../utils";

export default function DebriefingsTable({
  analysis,
  heading,
  debriefings,
  onStartReview,
  onContinueReview,
}) {
  return (
    <>
      <h3>{heading}</h3>
      <div style={{ overflowY: "auto", maxHeight: "200px" }}>
        <Table striped={true} borderless size="sm" hover>
          <colgroup>
            <col style={{ width: "20%" }} />
            <col style={{ width: "10%" }} />
            <col style={{ width: "20%" }} />
            <col style={{ width: "50%" }} />
          </colgroup>
          <thead>
            <tr>
              <th>Debriefing ID</th>
              <th>Reviewer</th>
              <th>Timestamp</th>
              <th></th>
            </tr>
          </thead>
          <tbody className="scroll">
            {debriefings.length === 0 ? (
              <tr>
                <td colSpan="4">
                  Currently, there aren't any {heading.toLowerCase()}
                </td>
              </tr>
            ) : (
              <>
                {debriefings.map((debrief) => (
                  <tr key={debrief.id}>
                    <td>{debrief.id}</td>
                    <td>{debrief.reviewer}</td>
                    <td>{stdDateTimeFormatToCustom(debrief.timestamp)}</td>
                    <td>
                      <Button
                        size="sm"
                        color="primary"
                        disabled={!onContinueReview}
                        onClick={() => onContinueReview(debrief.id)}
                      >
                        Continue Review
                      </Button>
                      <Button size="sm" color="danger">
                        Delete
                      </Button>
                    </td>
                  </tr>
                ))}
              </>
            )}
          </tbody>
        </Table>
      </div>
      <Button
        size="sm"
        color="primary"
        disabled={!onContinueReview}
        onClick={() => onStartReview(analysis)}
      >
        Create New Review
      </Button>
    </>
  );
}
