import { comprises } from "@meddbriefer/feedback-data/analysisData";

export function inferenceCheckingForAssessments(events, actions){

    //find the assessment events that can be inferrenced using
    //the rules in const comprises
    //if the assessment event was not logged but can be inferred 
    //then set the status to "inferred-done"

    for (const [id, rule] of Object.entries(comprises)){
        let occurrences = events.filter(e => e.id === id)
        if (occurrences.length === 0){
            let ids = []
            // make list to check
            rule["args"].forEach(arg => {
                let id = rule.prepend + "-" + arg
                ids.push(id)
            })
            let loggedIDs = events.filter(e => ids.includes(e.id))
            if ((rule.op === "and" && rule.type !== "reassessment-vitals" && loggedIDs.length === ids.length) ||
                (rule.op === "and" && rule.type === "reassessment-vitals" && (2 * loggedIDs.length) === ids.length) ||
                (rule.op === "or" && loggedIDs.length > 0)) 
            {
                //update the items status to "inferred-done"
                let actsToUpdate = actions.filter(e => e.id === id && e.timestamp === -1)
                actsToUpdate.forEach(act => {act.status = "inferred-done"})
            }
        }    
    }
    return actions
}