function EmailFieldRenderer({value}) {
  return value.includes('@') ? value.split('@')[0] : value
}

function ObsLogLinkRenderer({ value, clickHandler }) {
  return (
    <Button size="sm" color="primary" onClick={() => clickHandler(value)}>
      View
    </Button>
  );
}
